import { loadData } from '@/api/common'
import Swiper from '@/components/swiper'
import FootNav from '@/components/foot-nav'
import Scroll from '@/components/scroll'
import { imgUrl } from '@/api/url'
export default {
  components: {
    Swiper,
    FootNav,
    Scroll
  },
  data() {
    return {
      imgUrl,
      bannerList: [],
      swiperConf: {
        autoplay: true
      },
      mainNavs: [
        {
          label: '在线商城',
          imgUrl: require('./images/check.png'),
          link: '/index/components/antiFake'
        },
        {
          label: '防伪查询',
          imgUrl: require('./images/information.png')
          // link: '/index/components/antiFake'
        },
        {
          label: '关于我们',
          imgUrl: require('./images/vacation.png'),
          link: '/index/components/aboutUs'
        }
      ]
    }
  },
  created() {
    this.slideshow()
  },
  methods: {
    // ...mapMutations(['loginIn'])
    slideshow() {
      loadData('/rotate/page').then(data => {
        data.data.map(item => {
          this.bannerList.push(this.imgUrl + item.fileId)
        })
        console.log(this.bannerList)
      })
    }
  }
}



