import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  name: 'carrousel',
  components: {
    swiper,
    swiperSlide
  },
  props: {
    swiperSlides: {
      type: Array,
      default() {
        return []
      }
    },
    options: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  data() {
    return {
      swiperOption: Object.assign(this.options, {
        pagination: {
          el: '.swiper-pagination'
        }
        // spaceBetween: 20
      })
    }
  },
  mounted() {
    setInterval(() => {
      // console.log('simulate async data')
      //
    }, 3000)
  }
}



